@import '_variables';
@import '_mixins';
@import '~video.js/dist/video-js.css';
@import '~videojs-dock/dist/videojs-dock.css';

$assets-path: '../../assets/';
@import '../../sass/player/index';

.standalone-video-embed {
  margin: 0 !important;
  background-color: black;
}


.video-js-wrapper {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  width: 100%;
}


video {
  width: 100%;
}

video[src] {
  padding: 0 !important;
}

#video-wrapper {
  width: 100%;
  height: 100%;
}

.video-js .vjs-tech {
  position: absolute;
  height: auto;
}

.video-js {
  height: auto !important;

  position: relative;

  

  &.vjs-fullscreen {
    video {
      max-height: 100%;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}

.vjs-peertube {
  display: none !important;
}

.video-js {
  width: 100%;
  overflow: hidden;

  .vjs-poster {
    background-size: 100% auto;
  }

  @media screen and (max-width: 350px) {
    .vjs-play-control {
      padding: 0 5px !important;
      width: 25px !important;
    }

    .vjs-volume-control {
      display: none !important;
    }

    .vjs-volume-panel {
      width: 26px !important;
    }

    .vjs-peertube-link {
      padding: 0;
    }
  }
}

#placeholder-preview {
  position: absolute;
  top: 0;
  left: 0;
  background-size: 100% auto;
  width: 100%;
  height: 100%;
  background-position: 50% 50%;
}





